.Card{
    background-color: rgb(255, 255, 255);
    width: 100%;
    max-width : 300px;
    margin: 20px;
    height: 300px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.img-container{
    text-align: center;
    width: 100%;
    height: 60%;
    border-radius: 0 0 15px 15px;
    overflow: hidden;
}

.img-container img{
    width: 100%;
    height: 100%;
}