
.AddForm form{
    background-color: rgb(103, 160, 224);
    padding: 10px;
    width: 50%;
    max-width:900px ;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.AddForm form label{
    padding: 3px;
    font-weight: 600;
}

.AddForm form input{
    width: 100%;
    padding: 10px;
    font-size: 14px;
}

.AddForm form button{
    background: blue;
    padding: 10px;
    border: none;
    padding-inline: 20px;
    margin-top: 10px;
    color: white;
}